"use client";

import { useRouter } from "next/navigation";
import { useState, useEffect } from "react";

interface DayStatus {
  date: number;
  completed: boolean;
  streak: boolean;
}

export default function RachaPage() {
  const router = useRouter();
  const [currentMonth, setCurrentMonth] = useState(new Date());
  const [monthData, setMonthData] = useState<DayStatus[]>([]);
  
  // Simulate AI verification of completed days
  useEffect(() => {
    const generateMonthData = () => {
      const year = currentMonth.getFullYear();
      const month = currentMonth.getMonth();
      const daysInMonth = new Date(year, month + 1, 0).getDate();
      
      // Simulate AI-verified completion status
      const data: DayStatus[] = [];
      
      for (let i = 0; i < daysInMonth; i++) {
        const date = i + 1;
        const daysPassed = Math.floor((Date.now() - new Date(year, month, date).getTime()) / (1000 * 60 * 60 * 24));
        const completed = daysPassed >= 0 && Math.random() > 0.3; // Simulate some completed, some not
        const streak = completed && (i === 0 || data[i-1]?.streak);
        
        data.push({
          date,
          completed,
          streak
        });
      }
      
      setMonthData(data);
    };

    // Only run on client side to avoid hydration mismatch
    if (typeof window !== 'undefined') {
      generateMonthData();
    }
  }, [currentMonth]);

  const getDayOfWeek = (date: number) => {
    const day = new Date(currentMonth.getFullYear(), currentMonth.getMonth(), date).getDay();
    return ['D', 'L', 'M', 'M', 'J', 'V', 'S'][day];
  };

  const getMonthName = (date: Date) => {
    return date.toLocaleString('es-ES', { month: 'long' });
  };

  return (
    <main className="max-w-md mx-auto bg-white min-h-screen pb-20">
      <div className="p-6">
        <button 
          onClick={() => router.back()} 
          className="text-sm text-blue-500 mb-4 hover:text-blue-600 transition-colors"
        >
          &larr; Volver
        </button>
        
        <h1 className="text-3xl font-bold mb-2">P√°gina de Racha</h1>
        <p className="text-md text-gray-600 mb-8">
          Bienvenido a la secci√≥n de Racha. Aqu√≠ podr√°s ver tus estad√≠sticas, historial y gestionar tus rachas.
        </p>

        {/* Original Racha Interface */}
        <section className="space-y-6 mb-8">
          {/* Current Streak Card */}
          <div className="bg-gradient-to-br from-purple-50 to-blue-50 p-6 rounded-2xl shadow-sm border border-gray-100">
            <h2 className="text-xl font-semibold mb-3 text-gray-800">Racha Actual</h2>
            <div className="flex items-center justify-between">
              <div>
                <p className="text-4xl font-bold text-purple-600">7 d√≠as</p>
                <p className="text-sm text-gray-500 mt-1">¬°Sigue as√≠!</p>
              </div>
              <div className="text-4xl">üî•</div>
            </div>
          </div>

          {/* Stats Grid */}
          <div className="grid grid-cols-2 gap-4">
            <div className="bg-white p-4 rounded-xl shadow-sm border border-gray-100">
              <p className="text-sm text-gray-500">Mejor Racha</p>
              <p className="text-2xl font-semibold text-gray-800">15 d√≠as</p>
            </div>
            <div className="bg-white p-4 rounded-xl shadow-sm border border-gray-100">
              <p className="text-sm text-gray-500">Total Completado</p>
              <p className="text-2xl font-semibold text-gray-800">45 d√≠as</p>
            </div>
          </div>

          {/* Progress Section */}
          <div className="bg-white p-6 rounded-2xl shadow-sm border border-gray-100">
            <h3 className="text-lg font-semibold mb-4 text-gray-800">Progreso Semanal</h3>
            <div className="grid grid-cols-7 gap-2">
              {[
                { day: 'L', completed: true },
                { day: 'M', completed: true },
                { day: 'M', completed: true },
                { day: 'J', completed: true },
                { day: 'V', completed: true },
                { day: 'S', completed: true },
                { day: 'D', completed: true },
              ].map((item, index) => (
                <div key={index} className="flex flex-col items-center">
                  <div className={`w-8 h-8 rounded-full flex items-center justify-center mb-1
                    ${item.completed ? 'bg-purple-600 text-white' : 'bg-gray-100 text-gray-400'}`}>
                    {item.completed ? '‚úì' : ''}
                  </div>
                  <span className="text-xs text-gray-500">{item.day}</span>
                </div>
              ))}
            </div>
          </div>

          {/* Motivation Card */}
          <div className="bg-gradient-to-r from-purple-500 to-blue-500 p-6 rounded-2xl text-white">
            <h3 className="text-lg font-semibold mb-2">¬°Mant√©n tu racha!</h3>
            <p className="text-sm opacity-90">
              Cada d√≠a que mantienes tu racha te acerca m√°s a tus objetivos. 
              ¬°No rompas la cadena!
            </p>
          </div>
        </section>

        {/* Monthly Calendar View */}
        <div className="bg-white rounded-2xl shadow-sm border border-gray-100 p-6 mb-6">
          <div className="flex justify-between items-center mb-4">
            <h2 className="text-xl font-semibold text-gray-800">
              {getMonthName(currentMonth)} {currentMonth.getFullYear()}
            </h2>
            <div className="flex gap-2">
              <button 
                onClick={() => setCurrentMonth(new Date(currentMonth.getFullYear(), currentMonth.getMonth() - 1))}
                className="p-2 text-purple-600 hover:bg-purple-50 rounded-full"
              >
                &larr;
              </button>
              <button 
                onClick={() => setCurrentMonth(new Date(currentMonth.getFullYear(), currentMonth.getMonth() + 1))}
                className="p-2 text-purple-600 hover:bg-purple-50 rounded-full"
              >
                &rarr;
              </button>
            </div>
          </div>

          {/* Calendar Grid */}
          <div className="grid grid-cols-7 gap-1 mb-2">
            {['L', 'Ma', 'Mi', 'J', 'V', 'S', 'D'].map((day) => (
              <div key={day} className="text-center text-sm text-gray-500 py-2">
                {day}
              </div>
            ))}
          </div>

          <div className="grid grid-cols-7 gap-0 relative p-1 mb-4">
            {/* Empty cells for days before the 1st */}
            {Array.from({ length: new Date(currentMonth.getFullYear(), currentMonth.getMonth(), 1).getDay() }).map((_, i) => (
              <div key={`empty-${i}`} className="aspect-square"></div>
            ))}
            
            {/* Calendar days */}
            {monthData.map((day, i) => {
              const isToday = new Date().getDate() === day.date && 
                            new Date().getMonth() === currentMonth.getMonth() &&
                            new Date().getFullYear() === currentMonth.getFullYear();
              
              // Find streak groups
              const prevDayStreak = monthData[i-1]?.streak;
              const nextDayStreak = monthData[i+1]?.streak;
              const isStartOfStreak = day.streak && !prevDayStreak;
              const isEndOfStreak = day.streak && !nextDayStreak;
              const isMiddleOfStreak = day.streak && prevDayStreak && nextDayStreak;
              const isInStreak = day.streak;

              // Calculate the day's width to connect streaks
              const shouldExtendLeft = isInStreak && prevDayStreak;
              const shouldExtendRight = isInStreak && nextDayStreak;
              
              // Build the style classes
              // Calculate streak group position
              const streakPosition = isInStreak
                ? isStartOfStreak 
                  ? 'start'
                  : isEndOfStreak
                    ? 'end'
                    : 'middle'
                : 'none';

              // Base styles
              let dayStyle = 'relative flex items-center justify-center ';

              if (streakPosition !== 'none') {
                // Streak day styles
                const gradientBg = 'bg-gradient-to-r from-purple-500 to-purple-600';
                
                // Container styles for streak days
                dayStyle += `
                  ${gradientBg} text-white font-medium
                  ${streakPosition === 'start' ? 'rounded-l-full pl-4' : ''}
                  ${streakPosition === 'end' ? 'rounded-r-full pr-4' : ''}
                  ${streakPosition === 'middle' ? `${gradientBg} before:absolute before:inset-0 before:${gradientBg} before:-z-10` : ''}
                  relative z-10
                `;

                // Connect streak days
                if (streakPosition === 'middle' || streakPosition === 'end') {
                  dayStyle += ' -ml-2';
                }
                if (streakPosition === 'middle' || streakPosition === 'start') {
                  dayStyle += ' -mr-2';
                }
              } else {
                // Non-streak day styles
                dayStyle += `
                  ${day.completed ? 'bg-purple-100 text-purple-900' : 'bg-gray-50 text-gray-600'}
                  rounded-lg mx-1.5
                `;
              }

              // Additional states
              if (isToday) {
                dayStyle += ' ring-2 ring-purple-400 ring-offset-2 ring-offset-white';
              }

              dayStyle += ' hover:opacity-90 transition-opacity duration-200';
              
              return (
                <div 
                  key={day.date}
                  className={`aspect-square flex items-center justify-center text-sm ${dayStyle}`}
                >
                  {day.date}
                </div>
              );
            })}
          </div>
        </div>

        {/* Stats Cards */}
        <div className="grid grid-cols-2 gap-4 mb-6">
          <div className="bg-white p-4 rounded-xl shadow-sm border border-gray-100">
            <p className="text-sm text-gray-500">Racha Actual</p>
            <p className="text-2xl font-semibold text-purple-600">
              {monthData.filter(d => d.streak).length} d√≠as
            </p>
          </div>
          <div className="bg-white p-4 rounded-xl shadow-sm border border-gray-100">
            <p className="text-sm text-gray-500">D√≠as Completados</p>
            <p className="text-2xl font-semibold text-purple-600">
              {monthData.filter(d => d.completed).length} d√≠as
            </p>
          </div>
        </div>

        {/* AI Integration Notice */}
        <div className="bg-gradient-to-r from-purple-500 to-purple-600 p-6 rounded-2xl text-white">
          <div className="flex items-center gap-3 mb-2">
            <span className="text-2xl">ü§ñ</span>
            <h3 className="text-lg font-semibold">Verificaci√≥n por IA</h3>
          </div>
          <p className="text-sm opacity-90">
            Nuestra IA verifica autom√°ticamente el cumplimiento de tus objetivos diarios 
            para mantener tu racha actualizada. ¬°Mant√©n el ritmo!
          </p>
        </div>
      </div>
    </main>
  );
}
